"use client";

import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import {
    Form,
    FormControl,
    FormDescription,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { registerStudio, fetchStudioProfile } from "@/actions/studio";
// import { useToast } from "@/hooks/use-toast";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

// Validation Schema
const formSchema = z.object({
    storeName: z.string().min(1, "店舗名は必須です"),
    companyName: z.string().optional(),
    representative: z.string().min(1, "代表者は必須です"),
    manager: z.string().min(1, "責任者は必須です"),
    contactPerson: z.string().min(1, "担当者は必須です"),
    address: z.string().min(1, "住所は必須です"),
    phone: z.string().min(1, "電話番号は必須です"),
    email: z.string().email("メールアドレスの形式が正しくありません"),
    businessHours: z.object({
        weekday: z.string().min(1, "平日の営業時間は必須です"),
        saturday: z.string().min(1, "土曜の営業時間は必須です"),
        sundayHoliday: z.string().min(1, "日祝の営業時間は必須です"),
    }),
    url: z.string().url("URLの形式が正しくありません"),
    studioCount: z.coerce.number().min(1, "スタジオ数は1以上で入力してください"),
    options: z.string().optional()
});

export default function StudioRegisterPage() {
    const form = useForm<z.infer<typeof formSchema>>({
        resolver: zodResolver(formSchema) as any,
        defaultValues: {
            storeName: "",
            companyName: "",
            representative: "",
            manager: "",
            contactPerson: "",
            address: "",
            phone: "",
            email: "",
            businessHours: {
                weekday: "10:00 - 24:00",
                saturday: "09:00 - 24:00",
                sundayHoliday: "09:00 - 22:00"
            },
            url: "",
            studioCount: 1,
            options: ""
        },
    });

    useEffect(() => {
        const loadData = async () => {
            const data = await fetchStudioProfile();
            if (data) {
                form.reset(data);
            }
        };
        loadData();
    }, [form]);

    async function onSubmit(values: z.infer<typeof formSchema>) {
        const res = await registerStudio(values);
        if (res.success) {
            alert("登録完了: " + res.message);
        } else {
            alert("エラー: " + res.message);
        }
    }

    return (
        <div className="min-h-screen bg-black text-white p-6 md:p-12 font-sans">
            <div className="max-w-4xl mx-auto space-y-8">
                <header>
                    <h1 className="text-3xl font-bold text-cyan-400 mb-2">店舗情報登録</h1>
                    <p className="text-gray-400">アプリに掲載する基本情報を設定します。</p>
                </header>

                <Form {...form}>
                    <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">

                        <Card className="bg-slate-900 border-white/10 text-white">
                            <CardHeader>
                                <CardTitle className="text-xl">基本情報</CardTitle>
                            </CardHeader>
                            <CardContent className="grid md:grid-cols-2 gap-6">
                                <FormField
                                    control={form.control}
                                    name="storeName"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>店舗名 *</FormLabel>
                                            <FormControl>
                                                <Input placeholder="アンチグラビティ スタジオ" {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="companyName"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>会社名</FormLabel>
                                            <FormControl>
                                                <Input placeholder="株式会社音楽" {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="studioCount"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>スタジオ数 *</FormLabel>
                                            <FormControl>
                                                <Input type="number" {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="url"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Webサイト URL *</FormLabel>
                                            <FormControl>
                                                <Input placeholder="https://example.com" {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                            </CardContent>
                        </Card>

                        <Card className="bg-slate-900 border-white/10 text-white">
                            <CardHeader>
                                <CardTitle className="text-xl">連絡先・担当者</CardTitle>
                            </CardHeader>
                            <CardContent className="grid md:grid-cols-3 gap-6">
                                <FormField
                                    control={form.control}
                                    name="representative"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>代表者 *</FormLabel>
                                            <FormControl>
                                                <Input {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="manager"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>責任者 *</FormLabel>
                                            <FormControl>
                                                <Input {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="contactPerson"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>担当者 *</FormLabel>
                                            <FormControl>
                                                <Input {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="email"
                                    render={({ field }) => (
                                        <FormItem className="col-span-1 md:col-span-2">
                                            <FormLabel>メールアドレス *</FormLabel>
                                            <FormControl>
                                                <Input type="email" {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="phone"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>電話番号 *</FormLabel>
                                            <FormControl>
                                                <Input {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="address"
                                    render={({ field }) => (
                                        <FormItem className="col-span-1 md:col-span-3">
                                            <FormLabel>住所 *</FormLabel>
                                            <FormControl>
                                                <Input {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                            </CardContent>
                        </Card>

                        <Card className="bg-slate-900 border-white/10 text-white">
                            <CardHeader>
                                <CardTitle className="text-xl">営業時間</CardTitle>
                            </CardHeader>
                            <CardContent className="space-y-4">
                                <FormField
                                    control={form.control}
                                    name="businessHours.weekday"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>平日 *</FormLabel>
                                            <FormControl>
                                                <Input {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="businessHours.saturday"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>土曜日 *</FormLabel>
                                            <FormControl>
                                                <Input {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="businessHours.sundayHoliday"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>日祝 *</FormLabel>
                                            <FormControl>
                                                <Input {...field} className="bg-slate-800 border-slate-700" />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                            </CardContent>
                        </Card>

                        <Card className="bg-slate-900 border-white/10 text-white">
                            <CardHeader>
                                <CardTitle className="text-xl">オプション設定</CardTitle>
                            </CardHeader>
                            <CardContent>
                                <FormField
                                    control={form.control}
                                    name="options"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>詳細・備考</FormLabel>
                                            <FormControl>
                                                <Textarea
                                                    placeholder="レンタル機材リストや特記事項など"
                                                    className="min-h-[100px] bg-slate-800 border-slate-700"
                                                    {...field}
                                                />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                            </CardContent>
                        </Card>

                        <div className="flex justify-end gap-4">
                            <Button type="button" variant="outline" className="text-black bg-white">キャンセル</Button>
                            <Button type="submit" className="bg-cyan-500 hover:bg-cyan-600 text-black font-bold px-8">保存する</Button>
                        </div>
                    </form>
                </Form>
            </div>
        </div>
    );
}
